<template>
    <h1>COBRO</h1>
    <hr/>
    <div class="row contenido">
        <categoria class="col-sm-12 col-md-6 col-lg-9 col-xl-9 mt-5" 
            v-for="c, index in categorias" :key="index"
            :nombre = "c.nombre"
            :clientes = "c.clientes"
        />

       

    </div>
</template>

<script>
import { defineAsyncComponent } from 'vue'
import { mapActions, mapGetters } from 'vuex'
export default {
    components:{
        categoria : defineAsyncComponent(()=> import ('@/modules/rifa/components/cobro/categoria.vue'))
    },
    methods:{
        ...mapActions('cliente',['getClientesDiaCobro'])
    },
    computed:{
        ...mapGetters('cliente',['categorias'])
    },
    created(){
        this.getClientesDiaCobro()
    }
}
</script>

<style scoped>

    .contenido{
        margin-left: 15%;
    }

</style>